
@media only screen and (min-width: 600px){
#foot{
      padding: 0 !important;
      height: 55px !important;
}
.maCont{
      padding-bottom: 21px;
}
}
@media only screen and (max-width: 599px){
#foot{
      padding: 0 !important;
      height: 75px !important;
}
.maCont{
      padding-bottom: 42px;
}
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.fade-leave-active {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
  opacity: 0;
}
.dchide{
  display: none !important;
}



