
.main{
  font-size: 150%;
}
.song-text{
    text-shadow: 4px 4px 8px black;
    background: rgba(1, 1, 1, .1);
}
.card__media[lazy=loading] {
  background: center center / cover no-repeat;
}
.card__media[lazy=loaded] {
  background: center center / cover no-repeat;
}
.offline{
  bottom: 0px;
  right: 0px;
}
.btns{
    position: absolute;
    bottom: 0;
}



