
.vol-slider {
    -webkit-appearance: none;
    width: 100%;
    height: 25px;
    background: #d3d3d3;
    outline: none;
    /* opacity: 1; */
    -webkit-transition: .2s;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
}
.vol-slider:hover {
    /* opacity: 1; */
}
.vol-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 25px;
    background: teal;
    cursor: pointer;
}
.vol-slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    background: teal;
    cursor: pointer;
}
#loadingSpinner{
  /* top:10px;   */
  width: 52px !important;
}
.slider-wrapper input {
  width: 150px;
  height: 20px;
  margin: 0;
  -webkit-transform-origin: 75px 75px;
          transform-origin: 75px 75px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.slider-wrapper {
  display: inline-block;
  width: 20px;
  height: 150px;
  padding: 0;
}
#dc-audio {
  display: none;
}
#dc-audio-container {
  width: 100%;
  margin-top: -12px;
}
.audio-controls {
  float: left;
}
#dc-player {
  padding-top: 5px;
}

/* #progress-slider {
  top: -8px
} */
#poster {
  height: 35px;
  margin-top: 6px;
}

/* #progress {
  height: 50px;
} */
#left {
  margin-top: 7px;
  margin-left: 7px;
  float: left;
}
#right {
  float: right;
  margin-top: 5px !important;
}
@media only screen and (min-width: 600px){
#middle {
    margin-right: 100px;
}
}
@media only screen and (max-width: 599px){
#progress-slider{
    padding: 0!important
}
#middle{
    float: left;
    width: 100%;
}
}




